/** COLUMN HORIZONTAL **/

.col-h-news span {
    width: 100%;
    display: inherit;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: rgba(235, 0, 0, 0);
    /* FF3.6-15 */
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 1%, rgba(0, 0, 0, 0.65) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='transparent', GradientType=0);
    /* IE6-9 */
    white-space: pre-line;
    padding: 15px 50px; 
    box-sizing: border-box; 
     color: #ffffff;
}

.col-h-news h1 {
    font-weight: 700;
    font-size: 1em;
    font-family: 'Open Sans', Tahoma, Geneva, Verdana, sans-serif;
    letter-spacing: 0.2px;
    text-transform: uppercase;
}

.col-h-news p {
    font-size: 1em;
}

